export default angular.module('deskworks.StaffHelpCtrl', [
  'ngRoute',
  'deskworks.helper',
  'deskworks.config',
])

  .controller('StaffHelpCtrl', function($scope, $timeout, $q, $route, $window, $translate, dwAlerts,
                                        helper, state, deskworksConfig) { 'use strict';
    const vm = this;

    vm.notice = state.getParam('notice');

    state.setPageTitle($translate.instant('SIMPLE_RES.STAFF_HELP.PAGE_TITLE'));

    /**
     * Prevent using Back button
     */
    $scope.$on('$routeChangeStart', (event, next, current) => {
      if (next.$$route && (
          next.$$route.controller === 'MemberSignUpBillingCtrl' || next.$$route.controller === 'MemberSignUpPurchaseCtrl' ||
          next.$$route.controller === 'SimpleReservationBillingCtrl' || next.$$route.controller === 'SimpleReservationPurchaseCtrl' ||
          next.$$route.controller === 'BuyPassBillingCtrl' || next.$$route.controller === 'BuyPassPurchaseCtrl')
      ) {
        dwAlerts.error($translate.instant('SIMPLE_RES.STAFF_HELP.BACK_ERROR'));
        event.preventDefault();
      }
    });

    /**
     * Next button handler
     */
    vm.home = function() {
      if (state.getParam('redirect')) { // redirect to splash page from Buy A Pass flow
        $window.location.href = state.getParam('redirect');
      } else {
        const origin = state.getParam('origin');
        const centerId = state.getCurrentCenterId();
        (origin ? state.logout() : $q.when())
          .finally(() => { // If origin presents, log out
            state.resetParams();
            if (origin) state.setCurrentCenterId(centerId);
            return state.setPath(origin || deskworksConfig.getDefaultRoute());
          });
      }

      return $timeout(10000);
    };
  });
