export default angular.module('deskworks.bundleReservation.confirm', [
  'deskworks.bundleReservation.BundleReservation'
])
  .controller('BundleReservationConfirmCtrl', function($scope, $route, $translate, BundleReservation,
                                                       helper, state, dwAlerts) { 'use strict';
    var vm = this;

    state.setPageTitle($translate.instant('RESERVATIONS.CONFIRM.PAGE_TITLE'));

    /**
     * Back button handler
     */
    vm.back = state.back;

    /**
     * Next button handler (make reservation without payment only if logged user is member or admin)
     */
    vm.next = function() {
      if(state.isLoggedMember() || state.isLoggedAdmin()) {
        var params = {
          centerId: state.getCurrentCenterId(),
          userId: state.getCurrentUserId(),
          reservationBundleId: $route.current.params.reservationBundleId
        };
        return BundleReservation.save(params).$promise
          .catch(function(err) { return dwAlerts.error(err, 'Failed to make reservation.'); })
          .then(function() { return state.setPath(state.getPath().replace(/\/confirm$/, '/complete')); });
      } else {
        return dwAlerts.error('Failed to make reservation without payment for non-member.');
      }
    };
  });
