/**
 * Need help? Phone 1-800-000-000.
 */
export default angular.module('needHelp', [])
  .directive('needHelp', function() {
    return {
      restrict: 'E',
      scope: true,
      controller: function() {},
      controllerAs: '$ctrl',
      template: '<span translate="GLOBAL.NEED_HELP" class="need-help" ng-if="$ctrl.phone" ' +
        'translate-values="{ phone: \'<a href=&quot;tel:\' + $ctrl.phone + \'&quot;>\' + $ctrl.phone + \'</a>\' }"></span>',
      bindToController: {
        phone: '<',
      },
    };
  });
