import template from './new-billing-profile.tpl.html';

/**
 * New Billing Profile
 */
export default angular.module('newBillingProfile', [])

  .directive('newBillingProfile', function() {
    return {
      template,
      restrict: 'E',
      scope: true,
      controller: 'NewBillingProfileCtrl as $ctrl',
      bindToController: {
        settings: '<',
        user: '<',
      },
    };
  })

  .controller('NewBillingProfileCtrl', function($scope, userProfileService) {
    const $ctrl = this;

    /**
     * $onInit
     */
    $ctrl.$onInit = () => {
      // filter credit card types according to settings
      $ctrl.settings.creditCardTypes = $ctrl.settings.creditCardTypes.filter(ccType => ccType.enable);

      $ctrl.user.creditCards.forEach(cc => {
        cc.type = $ctrl.settings.creditCardTypes.find(type => type.id === cc.type) || cc.type || {};
      });

      // add one CC if no payment methods
      $ctrl.user.creditCards = $ctrl.user.creditCards || [];
      if (!$ctrl.user.creditCards.length && !$ctrl.user.eChecks.length) {
        const cc = userProfileService.creditCard.new($ctrl.user);
        cc.type = $ctrl.settings.creditCardTypes[0];
        $ctrl.user.creditCards.push(cc);
      }

      $scope.$watchCollection(_ => $ctrl.user.creditCards.concat($ctrl.user.eChecks), methods => {
        if (methods && $ctrl.user.paymentMethodId && !methods.find(m => m.id === $ctrl.user.paymentMethodId)) {
          $ctrl.user.billingMethod = null; // reset billing/payment method when payment method deleted
          $ctrl.user.paymentMethodId = null;
        }
      });

      userProfileService.bindTestBillingGenerator($scope, $ctrl.user); // Bind test user data generator
    };

    $ctrl.isSingleMode = () => {
      const { creditCards, eChecks } = $ctrl.user;
      return !creditCards.length && (!eChecks.length || userProfileService.eCheck.isNew(eChecks[0])) ||
        !eChecks.length && (!creditCards.length || userProfileService.creditCard.isNew(creditCards[0]));
    };
  });
