/**
 * Bundle Reservation resource
 */
export default angular.module('deskworks.bundleReservation.BundleReservation', [
  'ngResource',
  'deskworks.config'
])
  .factory('BundleReservation', function($resource, deskworksConfig) { 'use strict';

    var url = deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/reservations/:reservationId');

    return $resource(url, { centerId: '@centerId', userId: '@userId', reservationBundleId: '@reservationBundleId' }, {
      save: { method: 'POST' }
    });
  });
