import needHelp from 'shared/need-help/need-help.directive';
import dwInvoice from "../../../shared/dw-invoice/dw-invoice.directive";

export default angular.module('deskworks.bundleReservation.purchase', [
  'ngRoute',
  'deskworks.helper',
  'deskworks.purchase.service',
  needHelp.name,
  dwInvoice.name,
])

  .controller('BundleReservationPurchaseCtrl', function($scope, $route, $translate, dwAlerts, helper, state,
                                                        reg, center, purchase, purchaseService) {
    const $ctrl = this;

    $ctrl.reg = reg;
    $ctrl.center = center;
    $ctrl.purchase = purchase;

    state.setPageTitle($translate.instant('SIMPLE_RES.PURCHASE.PAGE_TITLE', { centerName: center.name }));

    /**
     * Next button handler
     */
    $ctrl.next = function() {
      return state.setPath(state.getPath() + '/billing');
    };

    /**
     * Back button handler
     */
    $ctrl.back = state.back;

    /**
     * Staff Help button handler
     */
    $ctrl.staffHelp = function() {
      return purchaseService.createStaffHelp($ctrl.center.id, state.getCurrentUserId(), $route.current.params.purchaseId)
        .catch(err => dwAlerts.error(err, 'Failed to create staff help.'))
        .then(result => {
          state.setParam('notice', result.notice);
          return state.setPath(state.getPath() + '/staff-help');
        });
    };
  });
