/**
 * BundleReservationPurchase resource
 */
export default angular.module('deskworks.bundleReservation.BundleReservationPurchase', [
  'ngResource',
  'deskworks.config'
])
  .factory('BundleReservationPurchase', function($resource, deskworksConfig) { 'use strict';

    var url = deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/purchases/:purchaseId');

    return $resource(url, { centerId: '@centerId', userId: '@userId', reservationBundleId: '@reservationBundleId', reservation: true }, {
      save: { method: 'POST' }
    });
  });
