import newBillingProfile from 'shared/new-billing-profile/new-billing-profile.directive';

export default angular.module('deskworks.bundleReservation.billing', [
  'ngRoute',
  'deskworks.userProfile.service',
  'deskworks.purchase.service',
  'deskworks.bundleReservation.BundleReservation',
  'deskworks.bundleReservation.BundleReservationPurchase',
  'deskworks.routeHelper',
  'deskworks.helper',
  newBillingProfile.name,
])

  .controller('BundleReservationBillingCtrl', function($scope, $route, $translate,  dwAlerts, helper, state,
                                                       userProfile, reg, userProfileService, purchaseService,
                                                       BundleReservation, settings) {
    const vm = this;

    vm.userProfile = userProfile;
    vm.reg = reg;
    vm.settings = settings;

    // filter credit card types according to settings
    settings.creditCardTypes = settings.creditCardTypes.filter(ccType => ccType.enable);

    vm.userProfile.creditCards.forEach(cc => {
      cc.type = helper.find(settings.creditCardTypes, cc.type) || cc.type || {};
    });

    state.setPageTitle($translate.instant('BILLING_PROFILE.PAGE_TITLE',
      { userName: helper.join([vm.userProfile.firstName, vm.userProfile.lastName], ' ') }));

    // Require at least one CC or ACH
    vm.userProfile.creditCards = vm.userProfile.creditCards || [];
    if (!vm.userProfile.creditCards.length && !vm.userProfile.eChecks.length) {
      const cc = userProfileService.creditCard.new(vm.userProfile);
      cc.type = settings.creditCardTypes[0];
      vm.userProfile.creditCards.push(cc);
    }

    /**
     * Bind test user data generator
     */
    userProfileService.bindTestBillingGenerator($scope, vm.userProfile, vm.reg);

    vm.addCC = function() {
      const cc = userProfileService.creditCard.new(vm.userProfile);
      vm.userProfile.creditCards = [cc];
      vm.userProfile.eChecks = [];
    };

    vm.addECheck = function() {
      const check = userProfileService.eCheck.new(vm.userProfile);
      vm.userProfile.eChecks = [check];
      vm.userProfile.creditCards = [];
    };

    vm.showSwitchButton = function() {
      const cc = !vm.userProfile.creditCards[0] || userProfileService.creditCard.isNew(vm.userProfile.creditCards[0]);
      const ec = !vm.userProfile.eChecks[0] || userProfileService.eCheck.isNew(vm.userProfile.eChecks[0]);
      return cc && ec && vm.settings.enableEChecks;
    };

    /**
     * Next button handler
     */
    vm.next = function() {
      if (helper.pointFirstInvalid('#simpleReservationBilling')) return;

      return userProfileService.saveBillingProfile(state.getCurrentCenterId(), vm.userProfile)
        .then(() => {
          const params = {
            centerId: state.getCurrentCenterId(),
            userId: state.getCurrentUserId(),
            reservationBundleId: $route.current.params.reservationBundleId
          };

          return BundleReservation.save(params).$promise
            .catch(err => dwAlerts.error(err, 'Failed to make reservation.'))
            .then(reservation => purchaseService.createPayment(state.getCurrentCenterId(), state.getCurrentUserId(), $route.current.params.purchaseId, {})
              .catch(function(err) { return dwAlerts.error(err, 'Failed to make payment.'); })
              .then(function() { return state.setPath(state.getPath() + '/complete'); }));
        });
    };

    /**
     * Staff Help button handler
     */
    vm.staffHelp = function() {
      return purchaseService.createStaffHelp(state.getCurrentCenterId(), state.getCurrentUserId(), $route.current.params.purchaseId)
        .catch(err => dwAlerts.error(err, 'Failed to create staff help.'))
        .then(result => {
          state.setParam('notice', result.notice);
          return state.setPath(state.getPath().replace(/\/billing$/, '/staff-help'));
        });
    };

    /**
     * Back button handler
     */
    vm.back = state.back;
  });
