export default angular.module('deskworks.bundleReservation.userProfile', [
  'ngRoute',
  'deskworks.helper',
  'deskworks.userProfile.service',
  'deskworks.bundleReservation.BundleReservationPurchase'
])

  .controller('BundleReservationUserProfileCtrl', function($scope, $q, $route, $timeout, $translate,
                                                           dwAlerts, helper, state, reg, centers, userProfile,
                                                           userProfileService, setupService,
                                                           BundleReservationPurchase) { 'use strict';
    var vm = this;

    reg.new = !state.getLoggedUserId();
    vm.reg = reg;
    vm.userProfile = userProfile;
    vm.schema = userProfile.schema;

    state.setPageTitle(
      $translate.instant(vm.reg.new ? 'USER_PROFILE.NEW_USER.PAGE_TITLE' : 'USER_PROFILE.PAGE_TITLE',
        { userName: vm.userProfile.firstName + ' ' + vm.userProfile.lastName })
    );

    /**
     * Get settings to have 'Terms Of Service' stuff
     */
    setupService.getSetupSettings()
      .catch(function(err) { return dwAlerts.error(err, 'Failed to get settings.'); })
      .then(function(result) { vm.settings = result; });

    /**
     * Bind test user data generator
     */
    userProfileService.bindTestUserGenerator($scope, vm.userProfile, vm.reg);

    /**
     * Next button handler
     */
    vm.next = function() {
      if(helper.pointFirstInvalid('#userProfile')) return;

      return (vm.reg.new ? userProfileService.registerNewUser(0, vm.userProfile) : $q.when())
        .then(function() {
          if(vm.reg.new) { // Need to login under just created user
            return state.login(vm.userProfile.login, vm.userProfile.password)
              .catch(function(err) { return dwAlerts.error(err, 'Failed to login under created user.'); })
              .then(function() {
                state.replace();
                state.setCurrentCenterId(centers[0].id);
                return $timeout();
              });
          }
        })
        .then(function() {
          var params = {
            centerId: state.getCurrentCenterId(),
            userId: state.getCurrentUserId(),
            reservationBundleId: $route.current.params.reservationBundleId
          };
          return BundleReservationPurchase.save(params).$promise
            .catch(function(err) { return dwAlerts.error(err, 'Failed to create purchase for bundle reservation.'); });
        })
        .then(function(result) {
          return state.setPath(state.getPath() + '/purchase/' + result.id);
        });
    };

    /**
     * Back button handler
     */
    vm.back = state.back;
  });
