import 'shared/staff-help/staff-help.controller';
import staffHelpTpl from 'shared/staff-help/staff-help.tpl.html';
import userProfileTpl from 'shared/templates/new-user-profile.tpl.html';
import billingProfileTpl from 'shared/templates/new-billing-profile.tpl.html';

const index = ['$injector', function index($injector) {
  // JS
  const importAll = r => r.keys().map(r);
  const js = importAll(require.context('./', true, /\..+\/.+\.js$/));
  $injector.loadNewModules(js.map(m => m.default.name));

  // Templates
  $injector.invoke(['$templateCache', function($templateCache) {
    const ctx = require.context('./', true, /.*\.tpl\.html$/);
    ctx.keys().forEach(key => $templateCache.put(key.replace(/^\.\//, ''), ctx(key)));

    $templateCache.put('staff-help/staff-help.tpl.html', staffHelpTpl);
    $templateCache.put('templates/new-user-profile.tpl.html', userProfileTpl);
    $templateCache.put('templates/new-billing-profile.tpl.html', billingProfileTpl);
  }]);
}];

export default index;
